.red_O_front {
  background-color: red;
  box-shadow: 0 0 5vh red;
  color: white;
  text-shadow: 0 0 1vh white;
}
.redHover_front {
  background-color: red;
  box-shadow: 0 0 5vh red;
  transition: 0.2s ease-in-out;
}
.redNotHover_front {
  background-color: rgb(21, 21, 21);
  transition: 1s ease-in-out;
}
.red_O_front_win {
  filter: contrast(300%);
  background-color: color-mix(in srgb, orange, magenta);
  box-shadow: 0 0 15vh color-mix(in srgb, orange, magenta);
}

.blue_X_front {
  background-color: blue;
  box-shadow: 0 0 5vh blue;
  color: white;
  text-shadow: 0 0 1vh white;
}
.blueHover_front {
  background-color: blue;
  box-shadow: 0 0 5vh blue;
  transition: 0.2s ease-in-out;
}
.blueNotHover_front {
  background-color: rgb(21, 21, 21);
  transition: 1s ease-in-out;
}
.blue_X_front_win {
  filter: contrast(300%);
  background-color: color-mix(in srgb, cyan, purple);
  box-shadow: 0 0 15vh color-mix(in srgb, cyan, purple);
}

.clickNone_front {
  pointer-events: none;
}
