.bottomWallContainer {
  /* pink */
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 5%;
  width: 100%;
  height: 100%;
  background-color: black;
}

.bottomWallContainer__leftUpCorner {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30%;
  height: 30%;
  border-radius: 0% 0% 30% 0%;
  transition: 0.4s;
  color: transparent;
}
.bottomWallContainer__middleUp {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30%;
  height: 30%;
  border-radius: 0% 0% 30% 30%;
  transition: 0.4s;
  color: transparent;
}
