.red_O_bottom {
  background-color: red;
  box-shadow: 0 0 5vh red;
  color: white;
  text-shadow: 0 0 1vh white;
}
.redHover_bottom {
  background-color: red;
  box-shadow: 0 0 5vh red;
  transition: 0.2s ease-in-out;
}
.redNotHover_bottom {
  background-color: rgb(15, 15, 15);
  transition: 1s ease-in-out;
}
.red_O_bottom_win {
  filter: contrast(300%);
  background-color: color-mix(in srgb, orange, magenta);
  box-shadow: 0 0 15vh color-mix(in srgb, orange, magenta);
}

.blue_X_bottom {
  background-color: blue;
  box-shadow: 0 0 5vh blue;
  color: white;
  text-shadow: 0 0 1vh white;
}
.blueHover_bottom {
  background-color: blue;
  box-shadow: 0 0 5vh blue;
  transition: 0.2s ease-in-out;
}
.blueNotHover_bottom {
  background-color: rgb(15, 15, 15);
  transition: 1s ease-in-out;
}
.blue_X_bottom_win {
  filter: contrast(300%);
  background-color: color-mix(in srgb, cyan, purple);
  box-shadow: 0 0 15vh color-mix(in srgb, cyan, purple);
}

.clickNone_bottom {
  pointer-events: none;
}
