.upperMenuContainer {
  top: 0;
  left: 0;
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 5%;
  width: 100%;
  height: 100%;
  background-color: black;
}

img {
  width: 50%;
  height: 50%;
}

.upperMenuContainer__leftUpCorner {
  transform: rotate(-90deg);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 30%;
  height: 30%;
  font-size: 2vh;
  color: black;
  background-color: rgb(24, 24, 24);
  border-radius: 0% 0% 0% 30%;
}

.upperMenuContainer__middleUp {
  transform: rotate(-90deg);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 30%;
  height: 30%;
  font-size: 2vh;
  color: black;
  background-color: rgb(24, 24, 24);
  border-radius: 30% 0% 0% 30%;
}

.upperMenuContainer__righttUpCorner {
  transform: rotate(-90deg);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 30%;
  height: 30%;
  font-size: 2vh;
  color: black;
  border-radius: 30% 0% 0% 00%;
}

.upperMenuContainer__leftCenter {
  transform: rotate(-90deg);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 30%;
  height: 30%;
  font-size: 2vh;
  color: white;
  background-color: rgb(24, 24, 24);
  border-radius: 0% 0% 30% 30%;
}

.upperMenuContainer__middleCenter {
  transform: rotate(-90deg);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 30%;
  height: 30%;
  font-size: 3.5vh;
  color: white;
  background-color: rgb(24, 24, 24);
  border-radius: 30% 30% 30% 30%;
}

.upperMenuContainer__rightCenter {
  transform: rotate(-90deg);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 30%;
  height: 30%;
  font-size: 2vh;
  color: white;
  border-radius: 30% 30% 0% 0%;
}

.upperMenuContainer__leftdownCorner {
  display: flex;
