  font-size: 2vh;
  color: white;
  border-radius: 0% 30% 0% 0%;
}

.upperMenuContainer__centerDownCorner {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 30%;
  height: 30%;
  font-size: 2vh;
  color: white;
  border-radius: 30% 30% 0% 0%;
}

.upperMenuContainer__rightDownCorner {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 30%;
  height: 30%;
  font-size: 2vh;
  color: white;
  border-radius: 30% 0% 0% 0%;
}

.upperAbout {
  top: 0;
  left: 0;
  position: absolute;
  width: 65%;
  height: 30%;
  border-radius: 0% 0% 15% 0% / 0% 0% 30% 0%;
  background-color: blue;
  box-shadow: 0px 0px 5vh blue;
  color: white;
  text-shadow: 0px 0px 1vh white;
  transition: 0.5s;
}
.upperAbout_Off {
  opacity: 0;
  top: 0;
  left: 0;
  position: absolute;
  width: 65%;
  height: 30%;
  border-radius: 0% 0% 15% 0% / 0% 0% 30% 0%;
  background-color: blue;
  box-shadow: 0px 0px 5vh blue;
  color: white;
  text-shadow: 0px 0px 1vh white;
  transition: 0.5s;
}

.upperMenuContainer__RedHover {
  background-color: red;
  box-shadow: 0px 0px 5vh red;
  color: white;
  text-shadow: 0px 0px 1vh white;
  transition: 0.2s ease-in-out;
}
.upperMenuContainer__RedNotHover {
  background-color: rgb(24, 24, 24);
  color: black;
  transition: 0.4s ease-in-out;
}
.upperMenuContainer__RedGameMode {
  background-color: red;
  box-shadow: 0px 0px 5vh red;
  color: white;
  text-shadow: 0px 0px 1vh white;
}

.upperMenuContainer__BlueHover {
  background-color: blue;
  box-shadow: 0px 0px 5vh blue;
  color: white;
  text-shadow: 0px 0px 1vh white;
  transition: 0.2s ease-in-out;
}
.upperMenuContainer__BlueNotHover {
  background-color: rgb(24, 24, 24);
  color: black;
  transition: 0.4s ease-in-out;
}
.upperMenuContainer__BlueGameMode {
  background-color: blue;
  box-shadow: 0px 0px 5vh blue;
  color: white;
  text-shadow: 0px 0px 1vh white;
}

.upperMenuContainer__RoyalblueHover {
  background-color: Royalblue;
  box-shadow: 0px 0px 5vh Royalblue;
  color: white;
  text-shadow: 0px 0px 1vh white;
  transition: 0.2s ease-in-out;
}
.upperMenuContainer__RoyalblueNotHover {
  background-color: rgb(24, 24, 24);
  color: black;
  transition: 0.4s ease-in-out;
}
.upperMenuContainer__RoyalblueGameMode {
  background-color: Royalblue;
  box-shadow: 0px 0px 5vh Royalblue;
  color: white;
  text-shadow: 0px 0px 1vh white;
}

.whiteLoad_UpperMenu {
  animation-name: whiteLoadUpperAnim;
  animation-duration: 0.9s;
  animation-iteration-count: 1;
}

@keyframes whiteLoadUpperAnim {
  0% {
  }

  90%,
  100% {
    background-color: rgb(24, 24, 24);
    box-shadow: 0px 0px 0px;
    color: rgb(19, 19, 19);
    text-shadow: 0px 0px 0px;
  }
}
