.red_O_upper {
  background-color: red;
  box-shadow: 0 0 5vh red;
  color: white;
  text-shadow: 0 0 1vh white;
}
.redHover_upper {
  background-color: red;
  box-shadow: 0 0 5vh red;
  transition: 0.2s ease-in-out;
}
.redNotHover_upper {
  background-color: rgb(24, 24, 24);
  transition: 1s ease-in-out;
}
.red_O_upper_win {
  filter: contrast(300%);
  background-color: color-mix(in srgb, orange, magenta);
  box-shadow: 0 0 15vh color-mix(in srgb, orange, magenta);
}

.blue_X_upper {
  background-color: blue;
  box-shadow: 0 0 5vh blue;
  color: white;
  text-shadow: 0 0 1vh white;
}
.blueHover_upper {
  background-color: blue;
  box-shadow: 0 0 5vh blue;
  transition: 0.2s ease-in-out;
}
.blueNotHover_upper {
  background-color: rgb(24, 24, 24);
  transition: 1s ease-in-out;
}
.blue_X_upper_win {
  filter: contrast(300%);
  background-color: color-mix(in srgb, cyan, purple);
  box-shadow: 0 0 15vh color-mix(in srgb, cyan, purple);
}

.clickNone_upper {
  pointer-events: none;
}
