.bottomAbout {
  bottom: 0;
  left: 0;
  position: absolute;
  width: 65%;
  height: 30%;
  border-radius: 0% 15% 0% 0% / 0% 30% 0% 0%;
  background-color: blue;
  box-shadow: 0px 0px 5vh blue;
  color: white;
  text-shadow: 0px 0px 1vh white;
  transition: 0.5s;
}
.bottomAbout_Off {
  opacity: 0;
  bottom: 0;
  left: 0;
  position: absolute;
  width: 65%;
  height: 30%;
  border-radius: 0% 15% 0% 0% / 0% 30% 0% 0%;
  background-color: blue;
  box-shadow: 0px 0px 5vh blue;
  color: white;
  text-shadow: 0px 0px 1vh white;
  transition: 0.5s;
}

.bottomMenuContainer__RedHover {
  background-color: red;
  box-shadow: 0px 0px 5vh red;
  color: white;
  text-shadow: 0px 0px 1vh white;
  transition: 0.2s ease-in-out;
}
.bottomMenuContainer__RedNotHover {
  background-color: rgb(15, 15, 15);
  color: black;
  transition: 0.4s ease-in-out;
}
.bottomMenuContainer__RedGameMode {
  background-color: red;
  box-shadow: 0px 0px 5vh red;
  color: white;
  text-shadow: 0px 0px 1vh white;
}

