.leftMenuContainer {
  top: 0;
  left: 0;
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 5%;
  width: 100%;
  height: 100%;
  background-color: black;
}

img {
  width: 50%;
  height: 50%;
}

.leftMenuContainer__leftUpCorner {
  transform: rotate(-90deg);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 30%;
  height: 30%;
  font-size: 2vh;
  color: black;
  background-color: rgb(21, 21, 21);
  border-radius: 0% 0% 0% 30%;
  transition: 0.5s;
}
.leftMenuContainer__leftUpCorner_blue {
  transform: rotate(-90deg);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 30%;
  height: 30%;
  font-size: 2vh;
  background-color: blue;
  box-shadow: 0px 0px 5vh blue;
  color: white;
  text-shadow: 0px 0px 1vh white;
  transition: 0.5s;
  border-radius: 0% 0% 0% 30%;
}

