.leftScoreBoard {
  position: absolute;
  top: 0;
  width: 100%;
  height: 30%;
  background-color: blue;
  box-shadow: 0px 0px 5vh blue;
  color: white;
  text-shadow: 0px 0px 1vh white;
  transition: 0.5s;
}
.leftScoreBoard_Off {
  opacity: 0;
  position: absolute;
  top: 0;
  width: 100%;
  height: 30%;
  background-color: blue;
  box-shadow: 0px 0px 5vh blue;
  color: white;
  text-shadow: 0px 0px 1vh white;
  transition: 0.5s;
}

.leftMenuContainer__RedHover {
  background-color: red;
  box-shadow: 0px 0px 5vh red;
  color: white;
  text-shadow: 0px 0px 1vh white;
  transition: 0.2s ease-in-out;
}
.leftMenuContainer__RedNotHover {
  background-color: rgb(18, 18, 18);
  color: black;
  transition: 0.4s ease-in-out;
}
.leftMenuContainer__RedGameMode {
  background-color: red;
  box-shadow: 0px 0px 5vh red;
  color: white;
  text-shadow: 0px 0px 1vh white;
}

.leftMenuContainer__BlueHover {
  background-color: blue;
  box-shadow: 0px 0px 5vh blue;
  color: white;
  text-shadow: 0px 0px 1vh white;
  transition: 0.2s ease-in-out;
}
.leftMenuContainer__BlueNotHover {
