.secondMenuContainer__RedHover{
    background-color: red;
    box-shadow: 0px 0px 5vh red;
    color:white;
    text-shadow: 0px 0px 1vh white;
    transition: 0.2s ease-in-out;
}.secondMenuContainer__RedNotHover{
        background-color:rgb(18, 18, 18);
    color:black;
    transition: 0.4s ease-in-out;
}.secondMenuContainer__RedGameMode{
    background-color: red;
    box-shadow: 0px 0px 5vh red;
    color:white;
    text-shadow: 0px 0px 1vh white;
}

