.mainAppContainer {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
}

.roateContainer {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(10, 10, 10, 1) 50%,
    rgba(0, 0, 0, 1) 100%
  );
}

.cubeContainer {
  width: 1vw;
  height: 1vh;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transition: 1s;
}
.cubeContainerEndGame {
  scale: 0.8;
  transition: 1s;
}

@media only screen and (min-aspect-ratio: 1/1) {
  .cube {
    font-size: 8vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 42vh;
    height: 42vh;
    transform-style: preserve-3d;
    transform: rotate3d(0, 0, 0, 0deg);
  }
}
@media only screen and (max-aspect-ratio: 1/1) {
  .cube {
    font-size: 8vw;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 42vw;
    height: 42vw;
    transform-style: preserve-3d;
  }
}

@media only screen and (min-aspect-ratio: 1/1) {
  .cube_front {
    /* purple */
    transform: translateZ(20vh);
    position: absolute;
    width: 40.1vh;
    height: 40.1vh;
    opacity: 1;
  }
}
