@media only screen and (min-aspect-ratio: 1/1) {
  .buttonsContainer {
    position: absolute;
    top: 4vh;
    display: flex;
    justify-content: center;
    gap: 3vh;
    height: 6vh;
    width: 100%;
  }

  .returnButton {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 5vh;
    border-radius: 100px 100px 100px 100px;
    right: 25vw;
    height: 6vh;
    width: 6vh;
    background-color: rgb(21, 21, 21);
  }
  .returnButton:hover {
    width: 21.5vh;
    transition: 0.5s;
    background-color: magenta;
    box-shadow: 0 0 5vh magenta;
    color: white;
    text-shadow: 0 0 1vh white;
  }
  .returnButton:not(:hover) {
    transition: 0.5s;
  }
  .returnButtonEndGame {
    width: 21.5vh;
    transition: 0.5s;
    background-color: magenta;
    box-shadow: 0 0 5vh magenta;
    color: white;
    text-shadow: 0 0 1vh white;
  }

  .retryButton {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 5vh;
    border-radius: 100px 100px 100px 100px;
    right: 30vw;
    height: 6vh;
    width: 6vh;
    background-color: rgb(21, 21, 21);
  }
  .retryButton:hover {
    width: 24vh;
    transition: 0.5s;
    background-color: magenta;
    box-shadow: 0 0 5vh magenta;
    color: white;
    text-shadow: 0 0 1vh white;
  }
  .retryButton:not(:hover) {
    transition: 0.5s;
  }
}

